import React from "react";
import { Link } from "react-router-dom";
function Navbar() {
  return (
    <div>
      <header
        className="header-area header-sticky wow slideInDown shadow navbar-home"
        data-wow-duration="0.75s"
        data-wow-delay="0s"
      >
        <div className="container">
          <div className="row">
            <div className="col-12">
              <nav className="navbar navbar-expand-lg main-nav navbar-home">
                <a href="/" className="logo">
                  <img src="/assets/images/logo-removebg-preview.png" />
                </a>
                <button
                  className="navbar-toggler"
                  type="button"
                  data-toggle="collapse"
                  data-target="#navbarSupportedContent"
                  aria-controls="navbarSupportedContent"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
                  <img
                    src="/assets/images/menu-icon-24.png"
                    class="toggler-img-icon"
                  ></img>
                </button>

                <div
                  className="collapse navbar-collapse"
                  id="navbarSupportedContent"
                >
                  <ul className="navbar-nav">
                    <li className="nav-item active scroll-to-section">
                      <a className="nav-link active" href="#top">
                        HOME
                      </a>
                    </li>
                    <li className="nav-item scroll-to-section">
                      <a className="nav-link" href="#services">
                        WHO WE ARE
                      </a>
                    </li>
                    <li className="nav-item scroll-to-section">
                      <a className="nav-link" href="#portfolio">
                        WhAT WE DO
                      </a>
                    </li>
                    <li className="nav-item scroll-to-section">
                      <a className="nav-link" href="#blog">
                        OUR PRODUCTS
                      </a>
                    </li>
                    <li className="nav-item scroll-to-section">
                      <a className="nav-link" href="#How">
                        HOW WE DO IT
                      </a>
                    </li>
                    <li className="nav-item scroll-to-section">
                      <a className="nav-link" href="#reservation">
                        MESSAGE US
                      </a>
                    </li>
                    <li>
                      <div class="dropdown dropdown-home">
                        <button
                          class="btn dropdown-toggle"
                          type="button"
                          data-toggle="dropdown"
                          aria-expanded="false"
                        >
                          LANGUAGE
                        </button>
                        <div class="dropdown-menu drop-content">
                          <a class="dropdown-item text-right" href="#">
                            العربية
                          </a>
                          <a class="dropdown-item" href="/">
                            English
                          </a>
                        </div>
                      </div>
                    </li>
                  </ul>
                </div>
              </nav>
            </div>
          </div>
        </div>
      </header>
    </div>
  );
}

export default Navbar;
