import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import "swiper/css";

const images = [
  "/assets/images/meat.png",
  "/assets/images/vegtables_and_fruits.png",
  "/assets/images/seafood.png",
  "/assets/images/packaging.png",
  "/assets/images/poultry.png",
  "/assets/images/cleaning.png",
  "/assets/images/dairy.png",
  "/assets/images/meat.png",
  "/assets/images/disposables.png",
  "/assets/images/dry.png",
  "/assets/images/packaging.png",
];

const Carousel = () => {
  return (
    <div>
      <main className="product-home" id='blog'>
        <div className="container">
          <div className="row">
            <div className="col-md-12 text-center">
              <h1 className="mt-5">Hundreds of products</h1>
              <p>
                Replacing daily orders to many suppliers with a single order<br></br>
                placed through one platform Cleaning Disposables Packaging
                Beverages Dairy Bakery Dry
              </p>
              <Swiper
                modules={[Autoplay]}
                spaceBetween={20}
                breakpoints={{
                  1200:{
                    slidesPerView:9,
                  },
                  991:{
                    slidesPerView:7,
                  },
                  767:{
                    slidesPerView:5,
                  },
                  300:{
                    slidesPerView:3.5,
                  },
                  }}
                loop={true}
                autoplay={{ delay: 2000, disableOnInteraction: false }}
              >
                {images.map((src, index) => (
                  <SwiperSlide key={index}>
                    <img
                      src={src}
                      alt={`slide-${index}`}
                      style={{ width: "100%", height: "auto" }}
                      className="Carousel-img"
                    />
                  </SwiperSlide>
                ))}
              </Swiper>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
};

export default Carousel;
