import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import "swiper/css";

const data = [
  {
    img: "/assets/images/shop.png",
    title: "Browse Products",
    desc: "Choose from over a thousand different options in our online store.",
  },
  {
    img: "/assets/images/order.png",
    title: "Order or Schedule",
    desc: "Pick items then order for next day delivery or schedule at a later date",
  },
  {
    img: "/assets/images/receive.png",
    title: "Receive",
    desc: "Effortlessly track and receive shipments without the need for follow-ups.",
  },
];

const Carousel = () => {
  return (
    <div>
      <section className="how-home pb-5 d-block d-md-none" id="How">
        <div className="container">
          <div className="row">
            <div className="col-12 mt-5 text-center">
              <h1>How We Do It?</h1>
              <h2 className="mb-3 mt-2">
                Our approach to deliver success
              </h2>
            </div>

            <div className="col-12 text-center">
              <Swiper
                modules={[Autoplay]}
                loop={true}
                spaceBetween={30}
                slidesPerView={1}
              >
                {data.map((item, index) => (
                  <SwiperSlide key={index}>
                    <div className="text-center">
                      <img
                        src={item.img}
                        alt={item.title}
                        className=" rounded-xl"
                      />
                      <h3 className="">{item.title}</h3>
                      <p className="w-50 m-auto mt-3">{item.desc}</p>
                    </div>
                  </SwiperSlide>
                ))}
              </Swiper>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Carousel;
