import React from 'react'
import Carousel from './Carousel'
function HowWeDo() {
  return (
    <div>
      <section id="How">
        <section className="how-home pb-5 d-none d-md-block">
          <div className="container">
            <div className="row">
              <div className="col-md-12 mt-5 text-center">
                <h1>How We Do It?</h1>
                <h2 className="mb-md-3 mt-md-2">
                  Our approach to deliver success
                </h2>
              </div>

              <div className="col-md-4 text-center">
                <img src="/assets/images/shop.png" />
                <h3>Browse Products</h3>
                <p>
                  Choose from over a thousand
                  <br /> different options in our online store.
                </p>
              </div>
              <div className="col-md-4 text-center">
                <img src="/assets/images/shop.png" />
                <h3>Order or Schedule</h3>
                <p>
                  Pick items then order for next day delivery <br />
                  or schedule at a later date
                </p>
              </div>

              <div className="col-md-4 text-center">
                <img src="/assets/images/shop.png" />
                <h3>Receive</h3>
                <p>
                  Effortlessly track and receive shipments <br /> without the
                  need for follow-ups.
                </p>
              </div>
            </div>
          </div>
        </section>
      </section>

      <Carousel />
    </div>
  );
}

export default HowWeDo