import React from 'react'

function MessageUS() {
  return (
    <div>
      <section className="section message-home pt-lg-5" id="reservation">
        <div className="container">
          <div className="row">
            <div className="col-lg-6">
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d13820.643021358146!2d31.436575995933104!3d30.003540149103717!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x14583cc75436d909%3A0x7f921d4528ec3e03!2z2KfZhNiq2KzZhdi5INin2YTYrtin2YXYs9iMINin2YTZhdmG2LfZgtipINin2YTYtdmG2KfYudmK2KnYjCDZgtiz2YUg2KPZiNmEINin2YTZgtin2YfYsdipINin2YTYrNiv2YrYr9ip2Iwg2YXYrdin2YHYuNipINin2YTZgtin2YfYsdip4oCs!5e0!3m2!1sar!2seg!4v1753646789854!5m2!1sar!2seg"
                width={600}
                height={587}
                style={{ border: 0 }}
                allowFullScreen=""
                loading="lazy"
                referrerPolicy="no-referrer-when-downgrade"
              />
            </div>
            <div className="col-lg-6 col-12">
              <div className="contact-form w-100">
                <form id="contact" action="" method="post">
                  <div className="row">
                    <div className="col-lg-12">
                      <h4>Message Us</h4>
                    </div>
                    <div className="col-lg-6 col-sm-12">
                      <fieldset>
                        <input
                          name="name"
                          type="text"
                          id="name"
                          placeholder="First Name*"
                          required=""
                        />
                      </fieldset>
                    </div>
                    <div className="col-lg-6 col-sm-12">
                      <fieldset>
                        <input
                          name="lastname"
                          type="text"
                          id="name"
                          placeholder="Last Name*"
                          required=""
                        />
                      </fieldset>
                    </div>
                    <div className="col-lg-12 col-sm-12">
                      <fieldset>
                        <input
                          name="email"
                          type="text"
                          id="email"
                          pattern="[^ @]*@[^ @]*"
                          placeholder="Your Email Address"
                          required=""
                        />
                      </fieldset>
                    </div>
                    <div className="col-lg-12 col-sm-12">
                      <fieldset>
                        <input
                          name="phone"
                          type="text"
                          id="phone"
                          placeholder="Phone Number*"
                          required=""
                        />
                      </fieldset>
                    </div>
                    <div className="col-lg-12">
                      <fieldset>
                        <textarea
                          name="message"
                          rows={6}
                          id="message"
                          placeholder="Message"
                          required=""
                          defaultValue={""}
                        />
                      </fieldset>
                    </div>
                    <div className="col-lg-12">
                      <fieldset>
                        <button
                          type="submit"
                          id="form-submit"
                          className="main-button-icon"
                        >
                          Make A Reservation
                        </button>
                      </fieldset>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>


        <footer className='footer-home mt-md-5'>
          <div className="container">
            <div className="row">
              <div className="col-lg-6 col-md-6 col-12">
                <p className="copyright">
                  Copyright © 2020 Art Factory Company
                </p>
              </div>
              <div className="col-lg-5 col-md-6 col-12">
                <ul className="social">
                  <li>
                    <a href="#">
                      <i className="fa fa-facebook" />
                    </a>
                  </li>
                  <li>
                    <a href="#">
                      <i className="fa fa-twitter" />
                    </a>
                  </li>
                  <li>
                    <a href="#">
                      <i className="fa fa-linkedin" />
                    </a>
                  </li>
                  <li>
                    <a href="#">
                      <i className="fa fa-rss" />
                    </a>
                  </li>
                  <li>
                    <a href="#">
                      <i className="fa fa-dribbble" />
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </footer>
      </section>
    </div>
  );
}

export default MessageUS