import React from "react";

function Who() {
  return (
    <div>
      <div id="services" class="our-services section Who-home">
        <div class="container">
          <div class="row">
            <div
              class="col-lg-6 align-self-center wow fadeInLeft"
              data-wow-duration="1s"
              data-wow-delay="0.2s"
            >
              <div class="left-image">
                <img src="assets/images/services-left-image.png" alt="" />
              </div>
            </div>
            <div
              class="col-lg-6 wow fadeInRight"
              data-wow-duration="1s"
              data-wow-delay="0.2s"
            >
              <div class="section-heading text-white who-content text-left">
                <h2>Who We Are</h2>
                <h4 className="mt-3">
                  At One Order, we understand the challenges of sourcing and
                  procuring your much needed supplies on time. We are your
                  reliable business partner covering all your procurement needs.
                </h4>
              </div>
              <div class="row">
                <div class="col-lg-12 mt-5 who-content">
                  <h4>
                    We are your ONE STOP SHOP for sourcing top quality supplies
                    at the best prices through a streamlined digital ordering
                    process. Our vast network of vendors and large array of
                    top-quality products ensures that all your needs are covered
                    from start to finish.
                  </h4>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Who;
